<template>
  <div class="cart-page">
    <div class="cart-header">
      <h1>Shopping Cart</h1>
      <p v-if="cart.state.items.length" class="items-count">
        {{ cart.state.items.length }} {{ cart.state.items.length === 1 ? 'item' : 'items' }}
      </p>
    </div>

    <div class="cart-content" v-if="cart.state.items.length">
      <div class="cart-items">
        <TransitionGroup name="list">
          <div v-for="item in cart.state.items" 
               :key="item.id" 
               class="cart-item">
            <div class="item-info">
              <div class="item-image-container">
                <img 
                  :src="getImageUrl(item)" 
                  :alt="item.name"
                  @error="handleImageError"
                  class="item-image"
                >
              </div>
              <div class="item-details">
                <h3>{{ item.name }}</h3>
                <div class="item-meta">
                  <span v-if="item.set" class="set-badge">{{ item.set }}</span>
                  <span v-if="item.rarity" class="rarity-badge">{{ item.rarity }}</span>
                </div>
                <div class="item-price">${{ item.price.toFixed(2) }}</div>
              </div>
            </div>
            
            <div class="item-actions">
              <div class="quantity-controls">
                <button @click="updateQuantity(item.id, item.quantity - 1)" 
                        :disabled="item.quantity <= 1"
                        class="quantity-btn">
                  <span class="material-symbols-rounded">remove</span>
                </button>
                <span>{{ item.quantity }}</span>
                <button @click="updateQuantity(item.id, item.quantity + 1)"
                        class="quantity-btn">
                  <span class="material-symbols-rounded">add</span>
                </button>
              </div>
              <div class="item-total">${{ (item.price * item.quantity).toFixed(2) }}</div>
              <button class="remove-button" @click="removeItem(item.id)">
                <span class="material-symbols-rounded">delete</span>
              </button>
            </div>
          </div>
        </TransitionGroup>
      </div>

      <div class="cart-summary">
        <h2>Order Summary</h2>
        <div class="summary-details">
          <div class="summary-row">
            <span>Subtotal</span>
            <span>${{ cart.state.total.toFixed(2) }}</span>
          </div>
          <div class="summary-row">
            <span>Shipping</span>
            <span>Free</span>
          </div>
          <div class="summary-total">
            <span>Total</span>
            <span>${{ cart.state.total.toFixed(2) }}</span>
          </div>
        </div>
        <button 
          @click="checkout"
          class="checkout-button"
        >
          <span class="material-symbols-rounded">shopping_bag</span>
          Checkout
        </button>
      </div>
    </div>

    <div v-else class="empty-cart">
      <span class="material-symbols-rounded">shopping_cart</span>
      <h2>Your cart is empty</h2>
      <p>Looks like you haven't added anything to your cart yet</p>
      <router-link to="/store" class="continue-shopping">
        Continue Shopping
      </router-link>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'
import { useRouter } from 'vue-router'
import cart from '@/store/cart'
import { getProxiedImageUrl, handleImageError } from '@/utils/imageProxy'

export default defineComponent({
  name: 'Cart',
  setup() {
    const router = useRouter()

    function getImageUrl(item) {
      return getProxiedImageUrl(item.image_url)
    }

    function updateQuantity(itemId, quantity) {
      if (quantity > 0) {
        cart.updateQuantity(itemId, quantity)
      }
    }

    function removeItem(itemId) {
      cart.removeItem(itemId)
    }

    function checkout() {
      router.push('/checkout')
    }

    return {
      cart,
      getImageUrl,
      handleImageError,
      updateQuantity,
      removeItem,
      checkout
    }
  }
})
</script>

<style scoped>
.cart-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 64px);
  background: var(--background-primary);
}

.cart-header h1 {
  font-size: 40px;
  font-weight: 700;
  letter-spacing: -0.025em;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 8px;
}

.items-count {
  color: var(--text-secondary);
  font-size: 16px;
  margin-top: 0;
}

.cart-content {
  display: grid;
  grid-template-columns: 1fr 320px;
  gap: 32px;
  margin-top: 32px;
}

.cart-items {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.02);
  transition: all 0.3s ease;
}

.cart-item:hover {
  background: rgba(255, 255, 255, 0.05);
}

.item-image-container {
  width: 80px;
  height: 112px;
  border-radius: 12px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.05);
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.item-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.item-info {
  display: flex;
  gap: 20px;
}

.item-details h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
}

.item-meta {
  margin-top: 8px;
  display: flex;
  gap: 8px;
}

.set-badge, .rarity-badge {
  padding: 4px 12px;
  border-radius: 100px;
  font-size: 12px;
  background: var(--background-elevated);
  color: var(--text-secondary);
}

.rarity-badge {
  background: var(--accent-primary);
  color: white;
}

.quantity-controls {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 4px;
  display: inline-flex;
  gap: 8px;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.quantity-btn {
  width: 28px;
  height: 28px;
  border: none;
  background: none;
  color: var(--text-primary);
  cursor: pointer;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.quantity-btn:hover:not(:disabled) {
  background: var(--accent-primary);
  color: white;
}

.quantity-btn:disabled {
  color: var(--text-tertiary);
  cursor: not-allowed;
}

.cart-summary {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 24px;
  position: sticky;
  top: 100px;
  height: fit-content;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.cart-summary h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 24px;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  color: var(--text-secondary);
  font-size: 15px;
}

.summary-total {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 18px;
  font-weight: 600;
}

.checkout-button {
  width: 100%;
  padding: 16px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  margin-top: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
}

.checkout-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(var(--accent-primary-rgb), 0.3);
}

.empty-cart {
  text-align: center;
  padding: 64px 24px;
  color: var(--text-secondary);
}

.empty-cart h2 {
  font-size: 24px;
  margin: 16px 0;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.continue-shopping {
  display: inline-block;
  margin-top: 24px;
  padding: 16px 32px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
}

.continue-shopping:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(var(--accent-primary-rgb), 0.3);
}

/* List transition animations */
.list-enter-active,
.list-leave-active {
  transition: all 0.3s ease;
}

.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(-30px);
}

@media (max-width: 768px) {
  .cart-content {
    grid-template-columns: 1fr;
  }
  
  .cart-summary {
    position: static;
  }
}
</style>
